"use client";
import React from "react";

const cards = [
  {
    title: "Designing",
    iconClass: "fa-solid fa-paintbrush",
    description: "Creative design solutions tailored to your brand.",
    color: "#89ec5b",
  },
  {
    title: "Development",
    iconClass: "fa-solid fa-code",
    description: "Robust and scalable web development services.",
    color: "#eb5ae5",
  },
  {
    title: "SEO",
    iconClass: "fa-brands fa-searchengin",
    description: "Boost your visibility with advanced SEO strategies.",
    color: "#5b98eb",
  },
  {
    title: "Marketing",
    iconClass: "fa-solid fa-bullhorn",
    description: "Innovative marketing that drives real results.",
    color: "#f59e0b",
  },
  {
    title: "Branding",
    iconClass: "fa-solid fa-gem",
    description: "Strong visual identity and branding assets.",
    color: "#8b5cf6",
  },
  {
    title: "Analytics",
    iconClass: "fa-solid fa-chart-line",
    description: "In-depth analytics to optimize your performance.",
    color: "#34d399",
  },
  {
    title: "Support",
    iconClass: "fa-solid fa-headset",
    description: "Dedicated support to help you anytime.",
    color: "#f43f5e",
  },
  {
    title: "Cloud",
    iconClass: "fa-solid fa-cloud",
    description: "Cloud solutions to scale your infrastructure.",
    color: "#38bdf8",
  },
];

export default function CardSection() {
  return (
    <section className="min-h-screen font-[Poppins] py-16 flex items-center justify-center bg-transparent">
      <div className="max-w-[1280px] w-full px-4">
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
          {cards.map((card, idx) => (
            <div
              key={idx}
              className="group relative w-full h-[360px] rounded-2xl border border-white/10 backdrop-blur-lg bg-white/5 shadow-lg overflow-hidden transition-all duration-500 hover:scale-105 hover:shadow-[0_0_0_3px_var(--clr)]"
              style={{ "--clr": card.color } as React.CSSProperties}
            >
              <div className="absolute inset-0 bg-gradient-to-br from-transparent to-[var(--clr)] opacity-0 group-hover:opacity-10 transition-opacity duration-500 pointer-events-none" />
              <div className="relative z-10 flex flex-col items-center justify-center h-full p-6 text-center space-y-6">
                <div className="text-[2.5rem] w-20 h-20 flex items-center justify-center rounded-full border-4 border-[var(--clr)] text-[var(--clr)] transition-transform duration-500 group-hover:scale-110">
                  <i className={card.iconClass}></i>
                </div>
                <h3 className="text-white text-xl font-semibold group-hover:text-[var(--clr)] transition-colors duration-300">
                  {card.title}
                </h3>
                <p className="text-sm text-gray-300 group-hover:text-white transition duration-300">
                  {card.description}
                </p>
                <a
                  href="#"
                  className="inline-block mt-2 px-5 py-2 border-2 border-[var(--clr)] text-white rounded-full transition-all duration-300 group-hover:bg-[var(--clr)] group-hover:text-black"
                >
                  Read More
                </a>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
